import { NVector3 } from '../../../lib';
export class Graph {
    nodes: Node[] = [];
    edges: Edge[] = [];
    constructor() {

        for (let i = 0; i < 6; ++i) {
            this.nodes.push(new Node(new NVector3(0, i, 0), NVector3.Origin(), 1));
        }
        this.nodes[0].fixed = true;
        for (let i = 0; i < this.nodes.length - 1; ++i) {
            this.edges.push(new Edge(this.nodes[i], this.nodes[i + 1]));
        }
        // this.edges.push(new Edge(this.nodes[0], this.nodes[this.nodes.length - 1]));
    }
    public AddNode(_p0: NVector3, _u0: NVector3, _mass: number) {
        const n: Node = new Node(_p0, _u0, _mass);
        this.nodes.push(n);
        return n;
    }

    public AddEdge(n0: Node, n1: Node) {
        const e: Edge = new Edge(n0, n1);
        this.edges.push(e);
        return e;
    }
    public Move(atr: NVector3, damping: number, g: number = -0.3, dt: number) {
        // Vector3d dv = atr - n.p;
        // double dist = dv.Length;
        // dv.Unitize();
        // //  n.f = dv * (1.0 / (dist));
        // n.f = dv * Math.Exp(-0.001 * dist * dist) * 0.1;
        // n.Move(dt, damping);

        for (let i = 0; i < this.nodes.length; ++i) {
            const n = this.nodes[i];
            // n.f = Vector3d.ZAxis * g;
            n.f.x *= g;
            n.f.y *= g;
            n.f.z *= g;

            const dv = new NVector3(atr.x - n.v.x, atr.y - n.v.y, atr.z - n.v.z);
            const L = dv.Length();
            dv.Unitize();
            n.f.x = dv.x * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.x * g;
            n.f.y = dv.y * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.y * g;
            n.f.z = dv.z * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.z * g;

            n.Move(dt, damping);
        }

        // foreach(Node n in nodes) {
        //     Vector3d dv = atr - n.p;
        //     double L = dv.Length;
        //     dv.Unitize();
        //     n.f = dv * Math.Exp(-0.001 * L * L) * 0.1 + Vector3d.ZAxis * g;
        //   }

        //   foreach(Edge e in edges) {
        //     e.ApplySpringForce();
        //   }

        //   foreach(Node n in nodes) {
        //     n.Move(dt, damping);
        //   }

        for (let i = 0; i < this.edges.length; ++i) {
            this.edges[i].ApplySpringForce();
        }

        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].Move(dt, damping);
        }
    }
    public Render(ctx: CanvasRenderingContext2D) {
        // ctx.fillStyle = '#000000';
        ctx.lineWidth = 0.1;
        for (let i = 0; i < this.edges.length; ++i) {
            ctx.beginPath();
            ctx.moveTo(this.edges[i].n0.v.x, this.edges[i].n0.v.y);
            ctx.lineTo(this.edges[i].n1.v.x, this.edges[i].n1.v.y);
            ctx.stroke();
        }

        for (let i = 0; i < this.nodes.length; ++i) {
            const p = this.nodes[i];
            ctx.beginPath();
            ctx.arc(p.v.x, p.v.y, p.m, 0, Math.PI * 2);
            ctx.closePath();
            ctx.stroke();
        }
    }
}
export class Edge {
    public n0: Node;
    public n1: Node;
    public targetLength: number = 1.05;
    public SpringConstant: number = 0.5;
    constructor(_n0: Node, _n1: Node) {
        this.n0 = _n0;
        this.n1 = _n1;
        this.targetLength = this.n0.v.Distance(this.n1.v);
    }

    public ApplySpringForce() {
        const dp: NVector3 = new NVector3(this.n1.v.x - this.n0.v.x, this.n1.v.y - this.n0.v.y, this.n1.v.z - this.n0.v.z);
        const dist: number = dp.Length();
        dp.Unitize();

        this.n0.f.x += dp.x * (dist - this.targetLength) * this.SpringConstant;
        this.n0.f.y += dp.y * (dist - this.targetLength) * this.SpringConstant;
        this.n0.f.z += dp.z * (dist - this.targetLength) * this.SpringConstant;

        this.n1.f.x -= dp.x * (dist - this.targetLength) * this.SpringConstant;
        this.n1.f.y -= dp.y * (dist - this.targetLength) * this.SpringConstant;
        this.n1.f.z -= dp.z * (dist - this.targetLength) * this.SpringConstant;
    }
}
export class Node {
    fixed: boolean = false;
    v: NVector3;
    u: NVector3;
    f: NVector3;
    m: number = 1;
    constructor(v: NVector3, u: NVector3, mass: number = 1) {
        this.v = v;
        this.u = u;
        this.f = NVector3.Origin();
        this.m = mass;
    }
    public Move(dt: number = 0.5, damping: number = 0.99) {
        if (this.fixed) { return; }
        this.u.x *= damping;
        this.u.y *= damping;
        this.u.z *= damping;

        this.u.x += this.f.x * (dt / this.m);
        this.u.y += this.f.y * (dt / this.m);
        this.u.z += this.f.z * (dt / this.m);

        this.v.x += this.u.x * dt;
        this.v.y += this.u.y * dt;
        this.v.z += this.u.z * dt;

        if (this.v.y < 0.0) {
            this.v.y = 0.0;
            if (this.u.y < 0.0) { this.u.y = -this.u.y; }
        }
    }
}