
export class CanvasCore {
    canvas: HTMLCanvasElement;
    host: HTMLElement;
    div: HTMLElement;
    ctx: CanvasRenderingContext2D;
    t: number = 0.0;
    width: number;
    height: number;
    constructor(id: string, width: number = 300, height: number = 200) {
        this.width = width;
        this.height = height;
        this.InitCanvas(id);
    }
    InitCanvas(id: string) {
        const canvas = document.createElement('canvas');
        const div = document.createElement('div');
        const host = document.getElementById(id);
        if (host && div) {
            this.div = div;
            this.div.style.width = this.width + 'px'; // this.host.clientWidth;
            this.div.style.height = this.height + 'px'; // this.host.clientHeight;
            this.div.style.margin = '10px';
            this.div.style.padding = '10px';
            this.div.style.marginBottom = '35px';
            div.appendChild(canvas);
            this.host = host;
            this.host.append(div);
        }
        if (canvas) {
            this.canvas = canvas;
            this.canvas.width = this.width; // this.host.clientWidth;
            this.canvas.height = this.height; // this.host.clientHeight;
            this.canvas.style.backgroundColor = '#aaaaaa';
            const ctx = this.canvas.getContext('2d');
            if (ctx) {
                this.ctx = ctx;
                this.InitUI();
                this.Init();
            }
        }
    }
    InitUI() {
        const btn = document.createElement('button');
        btn.textContent = 'reset';
        this.div.append(btn);

        const input = document.createElement('input');
        // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
        input.type = 'range';
        input.min = '0.1';
        input.max = '100.0';
        input.value = '1.5';

        this.div.append(input);
    }
    Init() {
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        this.ctx.scale(10, 10);
        this.EventBind();
        this.Loop();
    }
    EventBind() {
        this.canvas.onmousedown = (e: MouseEvent) => this.MouseDown(e);
        this.canvas.onmouseup = (e: MouseEvent) => this.MouseUp(e);
        this.canvas.onmousemove = (e: MouseEvent) => this.MouseMove(e);
        window.onresize = (e: UIEvent) => this.Resize(e);

    }
    Resize(e: UIEvent) {
        // console.log(e);
        this.canvas.width = this.host.clientWidth;
        this.canvas.height = this.host.clientHeight;
    }
    Loop() {
        requestAnimationFrame(() => { this.Loop(); });
        this.ctx.clearRect(-10, -10, this.ctx.canvas.width, this.ctx.canvas.height);
        this.ctx.fillStyle = '#dddddd';
        this.ctx.beginPath();
        this.ctx.rect(-500, -500, 1000, 1000);
        this.ctx.closePath();
        this.ctx.fill();
        this.Render(this.ctx);
    }
    Render(ctx: CanvasRenderingContext2D) {
        // console.log(ctx);
    }
    MouseDown(e: MouseEvent) {
        // console.log(e);
    }
    MouseUp(e: MouseEvent) {
        // console.log(e);
    }
    MouseMove(e: MouseEvent) {
        // console.log(e);
    }
}