import { ParticleSystemA } from './ParticleSystemA';
import { ParticleSystemB } from './ParticleSystemB';
import { ParticleSystemC } from './ParticleSystemC';
import { ParticleSystemD } from './ParticleSystemD';
export class Implementation {
    particleSystemA: ParticleSystemA;
    particleSystemB: ParticleSystemB;
    particleSystemC: ParticleSystemC;
    particleSystemD: ParticleSystemD;
    constructor(id: string) {
        this.particleSystemA = new ParticleSystemA(id);
        this.particleSystemB = new ParticleSystemB(id);
        this.particleSystemC = new ParticleSystemC(id);
        // this.particleSystemD = new ParticleSystemD(id);
    }
}