import { Graph } from './Core/Particle';
import { NVector3 } from '../../lib';
import { CanvasCore } from '../CanvasCore';
export class ParticleSystemA {
    CanvasCore: CanvasCore;
    particles: Graph[] = [];
    t = 0.9;
    m: NVector3 = new NVector3(0, 0, 0);
    constructor(id: string) {
        this.CanvasCore = new CanvasCore(id);

        this.particles.push(new Graph());

        this.Init();
    }
    Init() {
        this.CanvasCore.Render = (ctx: CanvasRenderingContext2D) => this.Render(ctx);
        this.CanvasCore.MouseDown = (e: MouseEvent) => this.MouseDown(e);
        this.CanvasCore.MouseUp = (e: MouseEvent) => this.MouseUp(e);
        this.CanvasCore.MouseMove = (e: MouseEvent) => this.MouseMove(e);
        // console.log('particle system');
    }
    Render(ctx: CanvasRenderingContext2D) {
        for (let i = 0; i < this.particles.length; ++i) {
            this.particles[i].Move(this.m, 0.9, -0.5, this.t);
            this.particles[i].Render(ctx);
        }
        ctx.fillStyle = 'rgba(255, 0, 0, 0.9)';
        ctx.beginPath();
        ctx.arc(this.m.x, this.m.y, 2.5, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
    }
    Grid(ctx: CanvasRenderingContext2D) {
        for (let i = -10; i < 10; ++i) {
            for (let j = -10; j < 10; ++j) {
                ctx.beginPath();
                ctx.fillStyle = 'rgba(255, 0, 0, 0.9)';
                ctx.arc(i, j, 0.05, 0, Math.PI * 2);
                ctx.closePath();
                ctx.fill();
            }
        }
    }
    mouseRemap(e: MouseEvent) {
        const bounding = this.CanvasCore.ctx.canvas.getBoundingClientRect()
        this.m.x = ((e.x - bounding.left) - this.CanvasCore.canvas.width * 0.5) * 0.1;
        this.m.y = ((e.y - bounding.top) - this.CanvasCore.canvas.height * 0.5) * 0.1;
    }
    MouseDown(e: MouseEvent) {
        this.mouseRemap(e);
        // console.log(e);
    }
    MouseUp(e: MouseEvent) {
        this.mouseRemap(e);
        // console.log(e);
    }
    MouseMove(e: MouseEvent) {
        this.mouseRemap(e);

    }
}