import { NVector3 } from '../../../lib';
export class Graph {
    particles: Particle[] = [];
    constructor() {

        for (let i = 0; i < 100; ++i) {
            this.particles.push(new Particle(20 * Math.random(), 20 * Math.random(), 0, 0.3 + Math.random() * 10));
        }
    }
    public Move(atr: NVector3, damping: number, g: number = -0.3, dt: number) {
        // Vector3d dv = atr - n.p;
        // double dist = dv.Length;
        // dv.Unitize();
        // //  n.f = dv * (1.0 / (dist));
        // n.f = dv * Math.Exp(-0.001 * dist * dist) * 0.1;
        // n.Move(dt, damping);

        for (let i = 0; i < this.particles.length; ++i) {
            const n = this.particles[i];
            // n.f = Vector3d.ZAxis * g;
            n.f.x *= g;
            n.f.y *= g;
            n.f.z *= g;

            const dv = new NVector3(atr.x - n.v.x, atr.y - n.v.y, atr.z - n.v.z);
            const L = dv.Length();
            dv.Unitize();
            n.f.x = dv.x * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.x * g;
            n.f.y = dv.y * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.y * g;
            n.f.z = dv.z * Math.exp(-0.001 * L * L) * 0.1; // + Vector3d.ZAxis.z * g;

            n.Move(dt, damping);
        }
    }
    public Render(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = 'rgba(0, 255, 0, 0.3)';
        for (let i = 0; i < this.particles.length; ++i) {
            const p = this.particles[i];
            ctx.beginPath();
            ctx.arc(p.v.x, p.v.y, 0.5 + p.m * 0.1, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
    }
}
export class Particle {
    v: NVector3;
    u: NVector3;
    f: NVector3;
    m: number = 1;
    constructor(x: number = 0, y: number = 0, z: number = 0, mass: number = 1) {
        this.v = new NVector3(x, y, z);
        this.u = NVector3.Origin();
        this.f = NVector3.Origin();
        this.m = mass;
    }
    public Move(dt: number = 0.5, damping: number = 0.99) {
        this.u.x *= damping;
        this.u.y *= damping;
        this.u.z *= damping;

        this.u.x += this.f.x * (dt / this.m);
        this.u.y += this.f.y * (dt / this.m);
        this.u.z += this.f.z * (dt / this.m);

        this.v.x += this.u.x * dt;
        this.v.y += this.u.y * dt;
        this.v.z += this.u.z * dt;

        if (this.v.y < 0.0) {
            this.v.y = 0.0;
            if (this.u.y < 0.0) { this.u.y = -this.u.y; }
        }
    }
}